import cls from './CustomTemplateModuleList.module.scss';
import { useTable } from 'react-table';
import { isMobile } from 'react-device-detect';
import { useState } from 'react';

export const CustomTemplateModuleList = props => {
	const { columns = [], layeredColumns = [], isReport, title, list = [], footer = [], onListItemClick = () => { }, onCellClick = () => { }, className, children, tableStyle, tableHostSimple, hideHeaders = false, modern, withColumns = true } = props;
	const [selected, setSelected] = useState(undefined);
	// const columnsF = isMobile ? columns.slice(0, 5) : columns;
const columnsF =columns;
	function Table({ columns, data }) {
		const { getTableProps, getTableBodyProps, headerGroups, rows, prepareRow } = useTable({
			columns,
			data,
		});
		return (
			<div className={title ? (isReport ? cls.tableReport : (tableHostSimple ? tableHostSimple : cls.tableHost)) : (tableHostSimple ? tableHostSimple : cls.tableHostSimple)} style={{ fontSize: isMobile ? '12px' : '16px', overflow: 'auto' }}>
				<table {...getTableProps()} size="sm" style={{ ...tableStyle }}>
					{withColumns && <thead style={{ display: hideHeaders ? 'none' : '', }}>
						{headerGroups.map((headerGroup, index) => (
							<tr key={index} {...headerGroup.getHeaderGroupProps()}>
								{headerGroup.headers.map((column, idx) => (
									<th key={idx} {...column.getHeaderProps()}>
										<div style={{ border: '0' }}>{column.render('Header')}</div>
									</th>
								))}
							</tr>
						))}
					</thead>}
					<tbody {...getTableBodyProps()}>
						{rows.map(
							(row, index) =>
								prepareRow(row) || (
									<tr
										key={index}
										{...row.getRowProps()}
										onClick={() => {
											setSelected(index);
											onListItemClick(list[row.index]);
										}}
										style={{ pointerEvents: list[index]?.enabled ? '' : list[index]?.enabled == false ? 'none' : '' }}
									>
										{row.cells.map((cell, idx) => {
											return (
												<td key={idx} {...cell.getCellProps()}
													onClick={e => {
														onCellClick({ cell: cell.render('Cell'), column: idx, row: index, data: list[row.index], top: e.clientX, left: e.clientY });
													}}
												>
													<div style={{ border: '0' }}>{cell.render('Cell')}</div>
												</td>
											);
										})}
									</tr>
								)
						)}
						{footer.length > 0 && (
							<tr>
								{(isMobile ? footer.slice(0, 3) : footer).map((f, i) => {
									return (
										<td key={i}>
											<div className={cls.footer}>{f}</div>
										</td>
									);
								})}
							</tr>
						)}
					</tbody>
				</table>
			</div>
		);
	}
	return (modern && layeredColumns.length > 0 ?

		<div style={{ width: '100%' }}>
			{children}
			{(list).map((row, index) => {
				const obj = {};
				layeredColumns.map(col => {
					obj[col.label] = row[col.label];
				})
				return (
					<div key={index} style={{ width: 'inherit', display: 'flex', flexDirection: 'column', justifyContent: 'center', padding: '20px', margin: '10px 0', fontFamily: 'var(--app-text-mobile-font)', border: isMobile ? '1px solid var(--app-color-gray-light)' : '1px solid var(--app-color-gray-light)' }}>
						{layeredColumns.map((col, i) => {

							if (`${obj[col.label]}`?.includes('jpg') || `${obj[col.label]}`?.includes('jpeg') || `${obj[col.label]}`?.includes('png')) {
								return <div className={cls.actionsContainer} >
									{withColumns && !(col.label === 'Actions' || col.label === 'Options') && <div style={{ width: '30%', textAlign: 'left', fontSize: isMobile ? '10px' : '14px', fontWeight: isMobile ? 'bolder' : 'normal', color: i == 0 ? 'var(--app-color-gray-darker)' : '', fontFamily: 'var(--app-text-mobile-font)' }}>{col.label}:</div>}
									<img onError={(e) => e.target.src = 'https://i.quotev.com/b2gtjqawaaaa.jpg'} src={obj[col.label]} style={{ width: withColumns ? '70%' : '100%', height: 'auto', cursor: 'pointer' }} />
								</div>;
							}

							return <div style={{ width: 'inherit' }} key={i}>
								{obj[col.label] && <div className={cls.actionsContainer} >
									{withColumns && !(col.label === 'Actions' || col.label === 'Options') && <div style={{ width: '30%', textAlign: 'left', fontSize: isMobile ? '10px' : '14px', fontWeight: isMobile ? 'bolder' : 'normal', color: i == 0 ? 'var(--app-color-gray-darker)' : '', fontFamily: 'var(--app-text-mobile-font)' }}>{col.label}:</div>}
									<div style={{ width: withColumns ? '70%' : '100%', textAlign: 'left', fontSize: isMobile ? '14px' : '16px', fontWeight: i == 0 ? '' : '', color: i == 0 ? 'var(--app-color-gray-darker)' : '', fontFamily: 'var(--app-text-mobile-font)' }}>{obj[col.label]}</div>

								</div>}
								{i < layeredColumns.filter(cF => obj[cF.label]).length - 1 && <hr style={{ borderTop: '0.5px solid #D2D2D2' }} />}
							</div>

						})}
					</div>
				);
			})}
		</div>
		:
		<div className={className ?? ''} app-variant-is-mobile={isMobile ? 'true' : 'false'} style={{ ...props.style, display: 'flex', flexDirection: 'column', margin: 'auto', overflow: 'auto' }}>
			<div style={{ marginBottom: '10px', width: '100%' }}>
				{children}
				{!title && columnsF.length > 0 && !modern && (
					<Table
						columns={columnsF.map(x => {
							return {
								Header: () => {
									return <span>{x}</span>;
								},
								accessor: x,
							};
						})}
						data={list}
					/>
				)}

				{title && columnsF.length > 0 && !modern && (
					<Table
						columns={[
							{
								Header: title,
								columns: columnsF.map(col => {
									return {
										Header: col,
										accessor: col,
									};
								}),
							},
						]}
						data={list}
					/>
				)}
				{layeredColumns.length > 0 && !modern &&
					<Table
						columns={layeredColumns.map(col => {
							if (col?.subColumns) {
								return {
									Header: col.label,
									columns: col.subColumns.map(col => {
										return {
											Header: col.label,
											accessor: col.label,
										};
									}),
								};
							}

							return {
								Header: () => {
									return <span>{col.label}</span>;
								},
								accessor: col.label,
							};


						})}
						data={list}
					/>
				}


			</div>






		</div>


	);
};

export default CustomTemplateModuleList;
