import { useEffect, useState } from 'react';
import { connect, useDispatch } from 'react-redux';
import { resetSelectedProStep, setSelectedProStep, setShowMenu, setSelectedProcess, setShowSideMenu } from '../../../../reduxStore/actions/flowActions';
import cls from './ProcessInfo.module.scss';
import { useNavigate } from 'react-router-dom';
import UserCard from '../../../common/UserCard/UserCard';
import HeaderTopBar from '../../../common/HeaderTopBar/HeaderTopBar';
import { Button, ModalR } from '../../../common';
import { APP_NAME } from '../../../../shared/utility';
import { TopMenu } from '../../../TopBar/Navigation/NavigationItems/MenuBar';
import StaticAccordionModule, { RenderSelectedFields } from '../../ModulesComp/StaticAccordionModule';
import { getComponentByIdPublic } from '../../../../services/components.service';
import { take } from 'rxjs';

const Components = props => {

	const { components } = props;
	const [companyData,] = useState(JSON.parse(localStorage.getItem('companyData')) ?? { 'name': '' });
	const [component, setComponent] = useState({ selected_fields: [] });

	//document.title = `${document.title} | ${APP_NAME}`;
	const dispatch = useDispatch();
	const navigate = useNavigate();

	useEffect(() => {
		dispatch(setShowSideMenu(false));
		dispatch(setShowMenu(true));
	}, []);

	return (
		<div className={cls.usersHost}>
			<TopMenu companyData={companyData} />
			<div style={{ justifyContent: 'space-between', display: 'flex', alignItems: 'center' }} >
				<HeaderTopBar headerTitle={document.title} targetProcessing={false} />

			</div>


			<br />
			<div className={cls.colleaguesSection}>
				<div className={cls.rightSideContainer}>
					{components?.map((item, index) => {
						return (
							<div key={index} style={{ padding: '10px' }}>
								<UserCard
									height={'25vh'}
									firstName={item.title}
									photo={Object.values(item.fields)?.find(f => f.includes('.png') || f.includes('.jpg'))}
									cardClicked={() => {
										getComponentByIdPublic(item.id, companyData?.id).pipe(take(1))
											.subscribe({
												next: data => {
													const cf = data.component_fields;
													cf.forEach(c => c.visible = true);
													setComponent({ ...data, component_fields: cf });

												},
											});

									}}
								/>

							</div>
						);
					})
					}
				</div>
				{component?.id &&<ModalR blured={true} scrollable={false} isOpen={true} shadowedContainerStyle={{ width: '80%' }} onClose={() => setComponent({ selected_fields: [] })} >
					<div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
						<RenderSelectedFields component={{ ...component, selected_fields: component.component_fields }} selectedComponent={{ ...component, show: true }} />
					</div>
				</ModalR>}

			</div>

		</div>
	);
}
const mapStateToProps = store => {
	const { selectedProcess, blueprints, allModules, companyProcesses, workflowTemplates, components } = store.flowReducer;

	return { selectedProcess, blueprints, allModules, companyProcesses, workflowTemplates, components };
};
const mapDispatchToProps = dispatch => {
	return {
		onSetSelectedProStep: selectedStep => dispatch(setSelectedProStep(selectedStep)),
		onResetSelectedProStep: () => dispatch(resetSelectedProStep()),
		onSetSelectedProcess: process => dispatch(setSelectedProcess(process))
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(Components);
